import React from "react";
import { Spinner, Center } from "@chakra-ui/react";
import { Title } from "react-head-meta";

function Loader() {
	return (
		<Center h="80vh">
			<Title title="KinoSearch" />
			<Spinner color="brand.500" size="xl" />
		</Center>
	);
}

export default Loader;
