import { extendTheme } from "@chakra-ui/react";
import "./css/theme.css";

const fonts = {
	heading: `'Manrope', sans-serif`,
	body: `'Manrope', sans-serif`,
};

const global = {
	body: {
		bg: "#25272A",
		color: "white",
	},
	button: {
		_focus: {
			boxShadow: "none",
		},
	},
	a: {
		_focus: {
			boxShadow: "none",
		},
	},
};
const colors = {
	brand: {
		"50": "#FFF6E5",
		"100": "#FFE7B8",
		"200": "#FFD78A",
		"300": "#FFC75C",
		"400": "#FFB82E",
		"500": "#FFA800",
		"600": "#CC8600",
		"700": "#996500",
		"800": "#664300",
		"900": "#332200",
	},
};

const config = {
	cssVarPrefix: "kinosearch",
	initialColorMode: "dark",
};

export default extendTheme({
	fonts,
	colors,
	config,
	styles: {
		global: global,
	},
});
