import React, { ReactNode } from "react";
import { Box, Heading, Link, Stack, Center, Input } from "@chakra-ui/react";
import { Link as RLink, useLocation, useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { SearchIcon } from "@chakra-ui/icons";

const Logo = () => (
	<RLink to="/">
		<Heading color="brand.500" size="lg">
			KinoSearch
		</Heading>
	</RLink>
);

const DesktopHeader = () => (
	<Stack
		direction="row"
		justifyContent="space-between"
		w="full"
		padding={3}
		paddingLeft={[3, 10]}
		paddingRight={[3, 10]}
	>
		<Logo />
		<Stack direction="row" spacing="20px">
			<MenuItem to="/contacts">Контакты</MenuItem>
			<SearchInput />
		</Stack>
	</Stack>
);

const MobileHeader = () => (
	<Stack
		direction="row"
		justifyContent="space-between"
		w="full"
		padding={3}
		paddingLeft={[3, 10]}
		paddingRight={[3, 10]}
	>
		<Logo />
		<Stack direction="row" spacing="20px">
			<MenuItem to="/search">
				<SearchIcon />
			</MenuItem>
		</Stack>
	</Stack>
);

const SearchInput = () => {
	type FormData = {
		q: string;
	};
	const { register, handleSubmit } = useForm<FormData>();
	const navigate = useNavigate();
	const onSubmit = handleSubmit((data) => {
		try {
			(document.getElementById("search_query") as HTMLInputElement).value = "";
		} catch (e) {
			console.log("Error:", e);
		}
		navigate(`/search?q=${data.q}`);
	});
	return (
		<form onSubmit={onSubmit}>
			<Input
				placeholder="Поиск"
				borderColor="#929292"
				borderRadius="20px"
				focusBorderColor="brand.500"
				_placeholder={{ fontSize: "16px", color: "white" }}
				id="search_query"
				{...register("q")}
			/>
		</form>
	);
};

interface ItemProps {
	children: ReactNode;
	to: string;
}

const MenuItem = (props: ItemProps) => {
	return (
		<Center>
			<Link
				color={(useLocation().pathname === props.to && "brand.500") || "white"}
				_hover={{
					color: `${
						(useLocation().pathname === props.to && "brand.500") || "brand.500"
					}`,
					textDecoration: "none",
				}}
				as={RLink}
				to={props.to}
			>
				{props.children}
			</Link>
		</Center>
	);
};

function Header() {
	const [width, setWidth] = React.useState<number>(window.innerWidth);
	React.useEffect(() => {
		const handleWindowSizeChange = () => {
			setWidth(window.innerWidth);
		};
		window.addEventListener("resize", handleWindowSizeChange);
		return () => {
			window.removeEventListener("resize", handleWindowSizeChange);
		};
	}, []);

	const isMobile = width <= 768;
	return (
		<Box
			w="full"
			bgColor="rgba(0, 0, 0, 0.5)"
			backdropFilter="blur(70px)"
			position="fixed"
			zIndex={3}
		>
			{(!isMobile && <DesktopHeader />) || <MobileHeader />}
		</Box>
	);
}

export default Header;
