import React, { Suspense, lazy } from "react";
import { Container } from "@chakra-ui/react";
import { HashRouter, Routes, Route } from "react-router-dom";
import Header from "./components/Header";
import Loader from "./components/Loader";
import ScrollToTop from "./components/assets/ScrollToTop";
import { Title } from "react-head-meta";

const Home = lazy(() => import("./pages/Home"));
const Search = lazy(() => import("./pages/Search"));
const Watch = lazy(() => import("./pages/Watch"));
const Film = lazy(() => import("./pages/Film"));

function App() {
	return (
		<HashRouter>
			<Header />
			<ScrollToTop />
			<Container
				maxW={["full", "full", "full", "full", "full", "8xl"]}
				// paddingInlineStart={[0, 0, 0, "1rem", "1rem", "1rem"]}
				// paddingInlineEnd={[0, 0, 0, "1rem", "1rem", "1rem"]}
				zIndex={2}
				paddingLeft={[3, 10]}
				paddingRight={[3, 10]}
				paddingTop={20}
				paddingBottom={20}
			>
				<Routes>
					<Route
						path="/"
						element={
							<Suspense fallback={<Loader />}>
								<Title title="Главная – KinoSearch" />
								<Home />
							</Suspense>
						}
					/>
					<Route
						path="/search"
						element={
							<Suspense fallback={<Loader />}>
								<Title title="Поиск – KinoSearch" />
								<Search />
							</Suspense>
						}
					/>
					<Route
						path="/film/:filmId"
						element={
							<Suspense fallback={<Loader />}>
								<Film />
							</Suspense>
						}
					/>
					<Route
						path="/watch/:filmId"
						element={
							<Suspense fallback={<Loader />}>
								<Watch />
							</Suspense>
						}
					/>
				</Routes>
			</Container>
		</HashRouter>
	);
}

export default App;
